@import url("https://fonts.googleapis.com/css2?family=Ga+Maamli&family=Sedan:ital@0;1&family=Work+Sans:ital,wght@0,100..900;1,100..900&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Exo+2:ital,wght@0,100..900;1,100..900&family=Ga+Maamli&family=Sedan:ital@0;1&family=Work+Sans:ital,wght@0,100..900;1,100..900&display=swap');

.projectcomp{
    height: 200px;
    display: flex;
    border: 2px solid black;
    border-radius: 40px;
    margin-bottom: 40px;
    overflow: hidden;
}
.pc-dtl-div{
    flex: 1;
    height: 100%;
    align-content: center;
}
.pc-dtl-div-inner{
    padding: 0 20px;
}
.pc-dtl-div-inner h1{
    font-family: "Sedan", serif;
    font-weight: 400;
    font-style: normal;
}
.pc-dtl-div-inner p{
    font-family: "Exo 2", sans-serif;
    font-weight: 400;
    font-style: normal;
    color: gray;
}
.pc-dtl-btn-a{
    background-color: rgb(0, 0, 0);
    color: white;
    border: none;
    border-radius: 20px;
    padding: 10px 20px;
    text-decoration: none;
    font-family: "Exo 2", sans-serif;
    font-weight: 400;
    font-style: normal;
}
.pc-dtl-btn-a:hover{
    color: black;
    background-color: white;
    transition-duration: 600ms;
    border: 1px solid  black;
}
.pc-img-div{
    flex: 1;
    height: 100%;
}
.pc-img{
    height: 100%;
    width: 100%;
    object-fit: cover;
}