@import url("https://fonts.googleapis.com/css2?family=Ga+Maamli&family=Sedan:ital@0;1&family=Work+Sans:ital,wght@0,100..900;1,100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Exo+2:ital,wght@0,100..900;1,100..900&family=Ga+Maamli&family=Sedan:ital@0;1&family=Work+Sans:ital,wght@0,100..900;1,100..900&display=swap");
.about-page {
  display: flex;
  justify-content: center;
  padding: 70px;
}
.about-page-inner {
  text-align: center;
  width: 80%;
}
.au-elmt {
  margin-bottom: 50px;
}
.au-elmt h2, .au-elmt h4{
    font-family: "Sedan", serif;
    font-weight: 800;
    font-style: normal;
}
.au-elmt p {
  font-size: 19px;
  color: rgb(104, 104, 104);
  font-family: "Exo 2", sans-serif;
  font-weight: 400;
  font-style: normal;
  color: gray;
}
.au-team-main-div {
  display: flex;
  justify-content: space-between;
  gap: inherit;
}
.au-team-main-div p {
  font-size: 16px;
}
.au-team-memb {
  width: 300px;
  height: 200px;
  align-content: center;
  display: flex;
  justify-content: center;
}
.au-team-memb-inner {
  align-content: center;
}
.au-team-memb-img-div {
  height: 100px;
  width: 100%;
}
.au-team-memb-img {
  height: 100%;
  width: 100px;
  border-radius: 50%;
}
.au-val {
  margin-bottom: 0px;
}

/* adding responsiveness */

@media screen and (max-width:768px){
  .about-page{
    margin-top: 40px;
    margin-bottom: 40px;
  }
  .au-team-main-div{
    display: flex;
    flex-direction: column;
  }
  .au-team-inner{
    display: flex;
    justify-content: center;
  }
}
