@import url('https://fonts.googleapis.com/css2?family=Ga+Maamli&family=Sedan:ital@0;1&family=Work+Sans:ital,wght@0,100..900;1,100..900&display=swap');
@font-face {
  font-family: 'AveriaSerifLibre';
  src:  url('../src/fonts/AveriaSerifLibre-Regular.ttf') format('truetype');
}
.header {
  background-color: black;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 20px;
  padding-left: 20px;
  color: black;
}
.hdr-logo-h1{
  color: white;
  font-family: "Sedan", serif;
  font-weight: 400;
  font-style: normal;
}
.hdr-nav {
  height: 100%;
  align-content: center;
  display: flex;
}
.hdr-nav-a {
  width: 100px;
  text-align: center;
  align-content: center;
  text-decoration: none;
  color: white;
  font-family: "Sedan", serif;
  font-weight: 400;
  font-style: normal;
}
.hdr-nav-a:hover {
  background-color: rgb(255, 255, 255);
  border-bottom: 6px solid rgb(1, 1, 1);
  transition: 300ms;
  color: black;
}
.hdr-nav-a:active {
  border-bottom: 6px solid rgb(255, 166, 2);
}