@import url("https://fonts.googleapis.com/css2?family=Ga+Maamli&family=Sedan:ital@0;1&family=Work+Sans:ital,wght@0,100..900;1,100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Exo+2:ital,wght@0,100..900;1,100..900&family=Ga+Maamli&family=Sedan:ital@0;1&family=Work+Sans:ital,wght@0,100..900;1,100..900&display=swap");
.home {
  display: flex;
  height: calc(100vh - 120px);
}
.hm-left-div {
  flex: 1;
  height: 100%;
  padding: 50px;
}
.hm-left-div-inner {
  height: 100%;
  align-content: center;
}
.hm-left-heading h1 {
  font-family: "Sedan", serif;
  font-weight: 800;
  font-style: normal;
}
.hm-left-dtl p {
  font-family: "Exo 2", sans-serif;
  font-weight: 400;
  font-style: normal;
  color: gray;
}
.hm-left-btns {
  display: flex;
  gap: 30px;
}
.hm-left-btn-a {
  color: white;
  background-color: rgb(0, 0, 0);
  border: none;
  border-radius: 20px;
  font-family: "Exo 2", sans-serif;
  font-weight: 400;
  font-style: normal;
  padding: 10px 20px;
  text-decoration: none;
}
.hm-left-btn-a:hover {
  color: black;
  background-color: white;
  transition-duration: 400ms;
  border: 1px solid black;
}

/* adding responsiveness */

@media screen and (max-width: 768px) {
  .home {
    height: fit-content;
    display: flex;
    flex-direction: column;
  }
  .hm-left-btns {
    display: flex;
    flex-direction: column;
  }
}
.hm-right-div {
  flex: 1;
  padding: 50px;
}
.hm-right-div-inner {
  height: 100%;
}
.hm-right-img-div {
  height: 100%;
  border-radius: 20px;
  overflow: hidden;
}
.hm-right-img {
  height: 100%;
  max-width: 100%;
  object-fit: cover;
}
