@import url("https://fonts.googleapis.com/css2?family=Ga+Maamli&family=Sedan:ital@0;1&family=Work+Sans:ital,wght@0,100..900;1,100..900&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Exo+2:ital,wght@0,100..900;1,100..900&family=Ga+Maamli&family=Sedan:ital@0;1&family=Work+Sans:ital,wght@0,100..900;1,100..900&display=swap');
.contact {
  background-color: hsl(0, 0%, 100%);
  display: flex;
  height: calc(100vh - 120px);
}
.ctt-left-div {
  flex: 7;
  height: 100%;
  padding: 50px;
}
.ctt-left-div-inner {
  font-weight: 600;
  color: rgb(0, 0, 0);
  align-content: center;
  height: 100%;
}
.ctt-right-div {
  flex: 5;
  height: 100%;
  padding: 50px;
}
.ctt-form-div {
  height: 100%;
  width: 100%;
}
.ctt-form {
  border-radius: 10px;
  padding: 20px;
}
.ctt-form-inner {
  display: flex;
  flex-direction: column;
}
input {
  padding: 10px;
  margin-bottom: 15px;
  border-radius: 20px;
  border: 1px solid gray;
}
input[type="submit"] {
  background-color: hsl(0, 0%, 0%);
  text-align: center;
  font-weight: 600;
  color: rgb(255, 255, 255);
  border-radius: 20px;
  border: none;
}
input[type="submit"]:hover {
  cursor: pointer;
  background-color: hsl(0, 0%, 100%);
  color: black;
  border: 1px solid black;
  transition-duration: 800ms;
}
.ctt-form-inner p{
    text-align: center;
    font-size: 14px;
}

/* adding responsiveness */

@media screen and (max-width:768px){
  .contact{
    height: fit-content;
    display: flex;
    flex-direction: column;
  }
}